@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.loading-container {
  width: 100%;
}
.loading-container .ant-spin-spinning {
  width: 100%;
}
.list-container .input-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}
.list-container .input-container .cancel-visible {
  width: 28px;
  margin-left: 11px;
  font-size: 12px;
  color: #000000;
  text-align: left;
  font-weight: 400;
}
.list-container .input-container .cancel-hidden {
  display: none;
}
.list-container .list-header {
  display: flex;
  flex-direction: column;
}
.list-container .list-header-text {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  text-align: left;
  font-weight: 500;
}
.list-container .list-header-text .color {
  background-color: #FCBE31;
  height: 4px;
  margin-top: 10px;
}
.list-container .list-header .list-divider {
  background-color: #DEDEDE;
  margin: 0 !important;
}
.list-container .card-list-container {
  width: 100%;
  background-color: white;
}
.list-container .foot-pagination {
  width: 100%;
  height: 90px;
  text-align: center;
}
.list-container .foot-pagination .ant-divider-horizontal {
  display: block;
  clear: both;
  width: 100%;
  min-width: 100%;
  height: 1px;
  margin: 18px 0;
}
.list-container .foot-pagination .ant-pagination.ant-pagination-mini .ant-pagination-prev,
.list-container .foot-pagination .ant-pagination-next {
  min-width: 20px;
  height: 20px;
  margin: 0px 8px;
  line-height: 17px;
  background: #FEBD43;
}
.list-container .foot-pagination .ant-pagination-item-active {
  font-weight: 500;
  background: #fff;
  border: none;
}
.list-container .foot-pagination .ant-pagination-item-active a {
  color: #FEBD43;
}
.list-container .foot-pagination .anticon {
  display: inline-block;
  color: white;
}
.searchImage {
  background: url(/static/media/search.d2fa2160.png) no-repeat center;
  background-size: contain;
  width: 16px;
  height: 16px;
}
.searchImage:hover {
  cursor: pointer;
}
