.tips-wrapper {
  width: 100%;
}
.tips-wrapper .header {
  font-size: 12px;
  color: #000000;
  text-align: left;
  font-weight: 400;
}
.tips-wrapper .divider {
  width: 100%;
  height: 1px;
  border-top: 1px solid #f6f6f6;
  margin-top: 4px;
}
.tips-wrapper .container {
  margin-top: 5px;
}
.tips-wrapper .container .tips-row {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 24px;
}
.tips-wrapper .container .tips-row .icon {
  font-size: 15px;
  margin-right: 36px;
}
.tips-wrapper .container .tips-row .tag {
  height: 24px;
  width: 70px;
  background: #F8F8F8;
  border-radius: 12px;
  text-align: center;
  font-size: 12px;
  color: #4F4F4F;
  font-weight: 400;
  line-height: 24px;
}
.tips-wrapper .container .tips-row .match0 {
  width: 116px;
}
.tips-wrapper .container .tips-row .match1 {
  width: 92px;
}
.tips-wrapper .container .tips-row .match2 {
  width: 78px;
}
.tips-wrapper .container .tips-row .schedule {
  width: 80px;
}
